/* 수정 예정 */
.swipe-item {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%; 
    background-size: cover;
  
    animation-name: swipe;
    animation-duration: 35s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    /* animation: slider 50s infinite ; */
    /* animation: fade-in 2s; */
    /* animation-duration : 5s; */
    /* animation-fill-mode: forwards; */
  }

  @keyframes swipe {
    0%{
      background: url("../../images/swipe01.jpg");
      background-repeat: no-repeat;
      background-size: cover;
    }
    20%{
      background: url("../../images/swipe02.JPG");
      background-repeat: no-repeat;
      background-size: cover;
    }
    60%{
      background: url("../../images/swipe03.jpg");
      background-repeat: no-repeat;
      background-size: cover;
    }
    80%{
      background: url("../../images/swipe04.jpg");
      background-repeat: no-repeat;
      background-size: cover;
    }
    100%{
      background: url("../../images/swipe05.jpg");
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  @keyframes fade-in {
    from {
      opacity: 0.5;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes fade-out {
    from {
      opacity: 1;
    }
    to {
      opacity: 0.5;
    }
  } 
