
.Info{
  position: absolute;
  top: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
  width: 100vw;
  padding: 20% 40px 10% 40px;

  box-sizing: border-box;
  background-color: #F7F1E5;
  color: #000000;
  font-family: var(--ko-font);
}
.desc{
  display: grid;
  align-content: space-between;
}
.skill{
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.skill-child {
  display: grid;
  gap: 60px
}
.goal {
  color: #035FC4 ! important;
  -webkit-text-stroke: 1px #035FC4 ;
  font-weight: 900;
  font-size: 2.7rem;
}

.contact{
  font-size: 1.3rem;
  font-weight: 600;
}
.stress{
  font-weight: 600;
  padding-bottom: 0;
}

.wrapper{
  padding-top: 20px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #646464;

  display: grid;
}

.heading{
  font-size: 1.4rem;
  font-weight: 600;
  color: rgb(23, 23, 23);
}

.sub{
  font-size: 1rem;
}