
.box {
  /* position: absolute; */
  /* margin: 15px; */
  /* margin-top: 50px; */
  width: 100%;
  padding: 0 40px;
  box-sizing: border-box;
  text-align: center;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  /* margin: 4rem 2vw; */
  padding: 0;
  list-style-type: none;
}

.card {
  display: block;
  cursor: pointer;
}

.card_active {
  display: block;
  cursor: pointer;

  position: relative;
  animation: fadeInUp linear;
  /* animation-timeline: view(); */
}

.card_active > div {
  width: 100%;
  height: 85%;
}

.card > div {
  display: grid;
}

.card__image {
  border-radius: 10px;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.card_title {
  font-family: var(--ko-font);
  font-size: 1rem;
  margin: -2px 0 0.3em;
  color: #a09f9f;
  /* padding: 0 0 15px 0; */
  text-align: center;
  align-items: start;
  font-weight: bold;
}

@media (max-width:450px){
  .box {
    margin: 5px;
    margin-top: 40px;
    text-align: center;
  }
  .cards {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  .card{
    padding: 0 15px;
  }
  .card__image {
    height: 30px;
    width: 30px;
  }

}

@keyframes fadeInUp {
  0% {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
  }
  to {
      opacity: 1;
      transform: translateZ(0);
  }
}
