.header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  text-align: start;

  padding: 40px;
  box-sizing: border-box;
  
  font-family: var(--default-font);
  font-size: 1.7rem;
  font-weight: 500;
  color: #101010;

  position: relative;
  z-index: 1;
}

.header > div > img {
  width: 25%;
}

.header > div:hover{
  cursor: pointer;
}

.header > ul {
  list-style: none;
  display: block;
  margin: 0;
  text-align: end;
}

.header > ul > li{
    cursor: pointer;
    padding-bottom: 5px;
    /* -webkit-text-stroke: 0.5px #868686; text outline*/
}

.active{
  color: #646464 ! important;
}