.Experience{
  display: grid;

  font-family: var(--default-font);
}

.Experience-wrapper{
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 5% 0;
  box-sizing: border-box;
}

.heading{
  margin-bottom: 40px;
  font-size: 3rem;
}

.cards{
  display: flex;
  gap: 15px;
  font-size: 1.5rem;
}
.cards-item {
  background-color: #646464;
  color: #e7e7e7;
  border-radius: 15px;
  padding: 5px 10px;
  box-sizing: border-box;
  font-size: 1rem;
}

.cards-item_active{
  background-color: #035FC4;
  color: #ffffff;
  border-radius: 15px;
  padding: 5px 10px;
  box-sizing: border-box;
  font-size: 1rem;
}

.cards-item:hover{
  cursor: pointer;
}
